.order-top-info {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
    background: #f8f9fb;

}

.product-list {
    margin-top: 15px;
    padding: 10px;

    .product-item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
}